import React, { useRef, useEffect } from "react";
import { useMachine } from "@xstate/react";
import { Modal } from "antd";

import {
  MaxIcon,
  MinIcon,
  PlayIcon,
  StopIcon,
  CloseIcon,
  OpenIcon
} from "../../utils/Icons";

import { videoPlayerMachine } from "../../machines/videoPlayerMachine";
import "./App.css";

function App() {
  const [state, send] = useMachine(videoPlayerMachine);
  const { url, playing } = state.context;

  const videoRef = useRef(null);

  const isClosed = state.matches("closed");
  const isNormal = state.matches("normal");
  const isMinimized = state.matches("minimized");

  // PLAY / PAUSE
  useEffect(() => {
    if (!videoRef.current) return;

    if (playing) {
      videoRef.current.play().catch(() => {});
    } else {
      videoRef.current.pause();
    }
  }, [playing]);

  const handleClose = () => {
    if (videoRef.current) videoRef.current.pause();
    send({ type: "CLOSE" });
  };

  const modalWidth = isMinimized ? 420 : 900;

  return (
    <div className={`video-app ${!isClosed ? "video-app-grey" : ""}`}>

      {/*   */}
      {isClosed && (
        <button className="open-button" onClick={() => send({ type: "OPEN" })}>
          <OpenIcon />
        </button>
      )}

      {/* MODAL       */}
      <Modal
        open={!isClosed}
        onCancel={handleClose}
        footer={null}
        width={modalWidth}
        centered
        bodyStyle={{ padding: 0 }}
        closable={false}
        maskClosable={true}
      >
        {/*   */}
        <div
          className={`video-player ${isMinimized ? "video-player--minimized" : ""}`}
        >

          <div className="video-app__title-container">
            <p className="video-app__title">Player</p>

            <button
              className="control-button control-button--close"
              onClick={handleClose}
            >
              <CloseIcon />
            </button>
          </div>

          <div className="video-player-container">
            <video
              ref={videoRef}
              src={url}
              className="video-player__video"
              onError={(e) => console.log(" :", e)}
              controls={false}
            />
          </div>

          <div className="video-player__controls">

            {/* PLAY / PAUSE */}
            <button
              className={playing
                ? "control-button control-button--pause"
                : "control-button control-button--play"
              }
              onClick={() =>
                playing ? send({ type: "PAUSE" }) : send({ type: "PLAY" })
              }
            >
              {playing ? <StopIcon /> : <PlayIcon />}
            </button>

            <div className="button-group">
              {isNormal && (
                <button
                  className="control-button control-button--minimize"
                  onClick={() => send({ type: "MINIMIZE" })}
                >
                  <MinIcon />
                </button>
              )}

              {isMinimized && (
                <button
                  className="control-button control-button--maximize"
                  onClick={() => send({ type: "MAXIMIZE" })}
                >
                  <MaxIcon />
                </button>
              )}
            </div>

          </div>
        </div>
      </Modal>
    </div>
  );
}

export default App;
